import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router);
import main from '@/views/Main.vue'
export default new Router({
  routes:[
    {
      path:'',
      component: () => import('@/views/Main.vue'),
    },

  ],
  //默认是hash模式，带#
  //改为history模式，不带#
  mode:'history'
})



